export default{
	props:["datas","state","ways"],
	data(){
		return{
			
		}
	},
	//过滤返回关键字
	filters:{
		Type(item){
			let Info;
			let Phone = /\d{11}/;
			if(Phone.test(item) && item.includes('正在派')){
				Info = "派";
			}else if(item.includes('已收取快件')){
				Info = "揽";
			}else if(item.includes('营业')){
				Info = "运";
			}else if(item.includes('丰巢智能快递柜')){
				Info = "待";
			}else if(item.includes('签收')){
				Info = "收";
			}else{
				Info = "";
			}
			return Info
		}
	},
	methods: {
		//判断哪个该展示关键字--还是默认
		Type(item){
			
			let Info;
			let Phone = /\d{11}/;
			if(Phone.test(item) && item.includes('正在派')){
				Info = true;
			}else if(item.includes('已收取快件')){
				Info = true;
			}else if(item.includes('营业')){
				Info = true;
			}else if(item.includes('丰巢智能快递柜')){
				Info = true;
			}else if(item.includes('签收')){
				Info = true;
			}else{
				Info = false;
			}
			return Info
		}
	},
}
